import {
  ChangeDetectorRef,
  Component,
  ElementRef,
  EventEmitter,
  Host,
  Input,
  OnChanges,
  OnDestroy,
  OnInit,
  Optional,
  Output,
  ViewChild,
  ViewEncapsulation
} from '@angular/core';
import { ControlValueAccessor, FormControl, NgControl } from '@angular/forms';
import { FormComponent } from '@sofico-framework/ui-kit/components/form';
import {
  OnSofFocus,
  SOF_FOCUS_COMPONENT
} from '@sofico-framework/ui-kit/directives/focus';
import { DateFormatEnum } from '@sofico-framework/utils';

import * as _moment from 'moment';
import { Moment } from 'moment';
import { NzDatePickerSizeType } from 'ng-zorro-antd/date-picker';
import { Changes, takeUntilDestroy, UntilDestroy } from 'ngx-reactivetoolkit';
import { combineLatest, Observable } from 'rxjs';
import { map, startWith } from 'rxjs/operators';

const moment = _moment;

@UntilDestroy()
@Component({
  selector: 'sof-input-date-picker',
  styleUrls: ['./input-date-picker.component.scss'],
  encapsulation: ViewEncapsulation.None,
  template: `
    <nz-date-picker
      #inputElement
      [nzInputReadOnly]="inputReadOnly"
      [@.disabled]="true"
      [formControl]="internalFormControl"
      [nzFormat]="dateFormat"
      [nzPlaceHolder]="placeHolder"
      [nzSeparator]="separator"
      [nzSize]="size"
      [nzDisabled]="isDisabled"
      [nzShowToday]="showToday"
      [nzDisabledDate]="disabledDate$ | async"
      [class.is-invalid]="
        invalid ||
        (ngControl?.invalid && (ngControl?.touched || form?.submitted))
      "
      (nzOnOpenChange)="onTouch($event)"
    ></nz-date-picker>
  `,
  providers: [
    { provide: SOF_FOCUS_COMPONENT, useExisting: InputDatePickerComponent }
  ]
})
export class InputDatePickerComponent
  implements OnInit, OnDestroy, OnChanges, ControlValueAccessor, OnSofFocus {
  // Size of Select input
  @Input() size: NzDatePickerSizeType = 'large';

  // DateFormat for enum
  @Input() dateFormat: DateFormatEnum = DateFormatEnum.BIG_ENDIAN_DASH;

  // Placeholder of date input
  @Input() placeHolder: string;

  // Separator
  @Input() separator = 'â†’';

  // Show the today button
  @Input() showToday = false;

  /**
   * The id of the input to connect to a label tag.
   * currently not supported
   */
  @Input() labelForId: string;

  /**
   *  Determines if the input is disabled.
   */
  @Input() isDisabled: boolean;

  /**
   * Determines whether the input is in a valid state.
   */
  @Input() invalid = false;

  /**
   * Determines the min date of the control.
   */
  @Input() minDate: Date;

  /**
   * Determines the max date of the control.
   */
  @Input() maxDate: Date;

  /**
   * EventEmitter that will emit the value when changed.
   */
  @Output() changeValue = new EventEmitter<Date>();

  /**
   * EventEmitter that will emit when control is touched.
   */
  @Output() touch = new EventEmitter<Date>();

  @Changes('minDate') minDate$: Observable<Date>;
  @Changes('maxDate') maxDate$: Observable<Date>;

  @ViewChild('inputElement', { read: ElementRef }) inputElement: ElementRef;

  // Sets the readonly attribute of the input tag (avoids virtual
  // keyboard on touch devices)
  // TODO seek a better solution for touch devices
  inputReadOnly = false;

  internalFormControl = new FormControl(null);
  propagateChange: any;
  propagateTouch: any;
  disabledDate$: Observable<(current: Date) => boolean>;

  constructor(
    @Optional() public form: FormComponent,
    @Optional() @Host() public ngControl: NgControl,
    private changeDetectorRef: ChangeDetectorRef
  ) {
    if (ngControl) {
      ngControl.valueAccessor = this;
    }
  }

  sofFocus(): void {
    this.inputElement.nativeElement.getElementsByTagName('input')[0].focus();
    this.changeDetectorRef.detectChanges();
  }

  ngOnInit(): void {
    this.internalFormControl.valueChanges
      .pipe(takeUntilDestroy(this))
      .subscribe(value => {
        if (!this.isDisabled) {
          this.changeValue.emit(value);

          if (this.propagateChange) {
            this.propagateChange(value);
          }
        }
      });

    this.disabledDate$ = combineLatest([
      this.minDate$.pipe(startWith(null)),
      this.maxDate$.pipe(startWith(null))
    ]).pipe(
      map(([minDate, maxDate]) => this.getNgZorroDisabledDate(minDate, maxDate))
    );
  }

  ngOnDestroy(): void {
    if (this.ngControl?.valueAccessor) {
      // Every time a control is re-created the previous writeValue reference(s) is not cleaned up.
      // So, over time, a lot of these references can be built up. This memory leak is a bug in Angular's implementation of ControlValueAccessor.
      // We hide that problem by assigning an empty function to writeValue every time we destroy the control.
      // An detailed explanation of the problem can be found here: https://github.com/angular/angular/pull/29335
      // The bug issue for it: https://github.com/angular/angular/issues/20007
      this.ngControl.valueAccessor.writeValue = () => {};
    }
  }

  ngOnChanges(): void {}

  registerOnChange(fn: any): void {
    this.propagateChange = fn;
  }

  registerOnTouched(fn: any): void {
    this.propagateTouch = fn;
  }

  writeValue(value: Date): void {
    this.internalFormControl.setValue(value, {
      emitEvent: false
    });
  }

  onTouch($event: boolean): void {
    if (!$event) {
      this.touch.emit();
      if (!this.isDisabled && this.propagateTouch) {
        this.propagateTouch();
      }
    }
  }

  private getNgZorroDisabledDate(
    minDate: Date,
    maxDate: Date
  ): (current: Date) => boolean {
    if (!minDate && !maxDate) {
      return () => false;
    } else if (!minDate && maxDate) {
      return (current: Date) => moment(current).isAfter(maxDate, 'day');
    } else if (minDate && !maxDate) {
      return (current: Date) => moment(current).isBefore(minDate, 'day');
    } else {
      return (current: Date) =>
        !moment(current).isBetween(minDate, maxDate, 'day', '[]');
    }
  }

  private formatMomentToDate(date: Moment): Date {
    return date.toDate();
  }
}
